/**
* src/lib/rbac.ts
* Role-based helpers and small policy helpers used across routes.
*/
import { prisma } from '@/lib/prisma';
import type { SessionUser } from './auth';


export function requireAuth(user: SessionUser | null) {
if (!user) throw new Error('unauthenticated');
}


export function requireRole(user: SessionUser | null, role: 'ADMIN' | 'MANAGER' | 'EMPLOYEE') {
requireAuth(user);
if (user!.role !== role) throw new Error('forbidden');
}


export async function isOrgAdmin(userId: string, orgId: string) {
const u = await prisma.user.findUnique({ where: { id: userId } });
return !!u && u.organizationId === orgId && u.role === 'ADMIN';
}


export async function isTeamManager(userId: string, teamId: string) {
const tm = await prisma.teamMember.findFirst({ where: { userId, teamId } });
return !!tm && tm.role === 'MANAGER';
}


export async function canViewTeam(userId: string, teamId: string) {
// admin of org or member of team
const tm = await prisma.teamMember.findFirst({ where: { userId, teamId } });
if (tm) return true;
const u = await prisma.user.findUnique({ where: { id: userId } });
return !!u && u.role === 'ADMIN' && !!u.organizationId;
}